.header {
    font-size: 1.4rem;
    height: 7rem;
    background-color: #fff;
    border-bottom: 1px solid var(--grey-light-2);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    height: 3.25rem;
    margin-left: 2rem;
}

.search {
    flex: 0 0 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    
    &__input {
        font-family: inherit;
        font-size: inherit;
        color: inherit;
        background-color: var(--grey-light-2);
        border: none;
        padding: .7rem .2rem;
        border-radius: 100px;
        width: 90%;
        transition: all .2s;
        margin-right: -3.25rem;
        padding-left: 1.5rem;

        &:focus {
            outline: none;
            width: 100%;
            background-color: var(--grey-light-3);
        }
        
        &::-webkit-input-placeholder {
            font-weight: 100;
            color: var(--grey-dark-3);
        }
    }

    &__input:focus + &__button {
        background-color: var(--grey-light-3);
    }

    &__button {
        border: none;
        background-color: var(--grey-light-2);

        &:focus {
            outline: none;
        }

        &:active {
            transform: translateY(2px);
        }
    }

    &__icon {
        height: 2rem;
        width: 2rem;
        fill: var(--color-complement-3);
    }
}

.user-nav {
    display: flex;
    align-items: center;
    align-self: stretch;

    & > * {
        padding: 0 2rem;
        cursor: pointer;
        height: 100%;
        display: flex;
        align-items: center;
    }

    & > *:hover {
        background-color: var(--grey-light-2);
    }

    &__icon-box {
        position: relative;
    }

    &__icon {
        height: 2.25rem;
        width: 2.25rem;
        fill: var(--grey-dark-2);
        
    }

    &__notification {
        font-size: .8rem;
        height: 1.75rem;
        width: 1.75rem;
        border-radius: 100px;
        background-color: var(--color-complement-3);
        color: #fff;
        position: absolute;
        top: 1.5rem;
        right: 1.1rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__user-photo {
        height: 3.75rem;
        border-radius: 50%;
        margin-right: 1rem;
    }
}